
.header {
    height: 80px;
}
.logo {
    float: left;
    padding: 22px 0 0 7px;
}
.logo img {
    width: 112px;
}
.nav{position: fixed;background: #a19d9d;height: 100%;height: calc(100% - 80px);width: 260px;right: -260px;z-index: 999;top: 80px;overflow-y:auto;padding-bottom: 80px;-webkit-transition: right 300ms ease-out;transition: right 300ms ease-out;}
.nav .nava{font-size: 20px;color: #ffffff;text-decoration: none;display: inline-block;cursor: pointer;width: 100%;padding: 20px 0 20px 52px;line-height: 1;border-bottom: 1px solid #d0cece;font-weight: bold;display: block;}
.nav .nava:last-child{border: 0}
.nav .nava:first-child{padding-top: 32px}
.nav .nava > a{color: #fff}
.nav .nava:hover{background: #b4b1b1}
.nav input{display: none;}
.nav input:checked+label{background: #e74e34;border-bottom: 0;padding-bottom: 14px;}
.nav input:checked+label:hover{background: #e74e34}
.nav input:checked+label dl{display: block;}
.nav .this{background: #e74e34;border-bottom: 0;padding-bottom: 14px}
.nav .this:hover{background: #e74e34}
.nav dl{font-size: 14px;line-height: 30px;display: none;}
.nav dl dt{font-weight: 400}
.nav dl a{color: #fff;}
.nav dl a:hover{color: #fff;text-decoration: underline;}
.nav .this dl{display: block;padding: 14px 0 0 0;margin: 0}
.nnav{font-size: 18px;float: left;line-height: 1;padding: 32px  0 4px 46px;overflow: hidden;}
.nnav a{color: #87a5b3;float: left;}
.nnav i{color: #87a5b3;padding: 2px 12px;float: left;font-size: 12px;}
.navrighrf{right: 0}

.mh{color: #a5bcc6;float: right;margin-right: 22px;text-align: center;font-size: 28px;padding-top:20px;    -webkit-transition: color 300ms ease-out;    transition: color 300ms ease-out;cursor: pointer;}
.mh:before{content: "\e621";font-size: 32px;}
.mh:active{color: #90a5ae}
.mh:hover{color: #b5d1dd}
.mh input{display: none;}
.icenter {
    float: right;
    color: #8fabb8;
    font-size: 14px;
    padding-top: 32px;
    margin-right: 22px;
    position: relative;
    cursor: pointer;
}

.icenter i {
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 7px solid #e3e3e3;
    float: right;
    margin: 8px 0 0 6px;
}

.icenter a {
    color: #87a5b3;
}

.icenter dl {
    border: 1px solid #d1e0e2;
    background: #fff;
    background: rgba(255, 255, 255, .90);
    position: absolute;
    font-size: 12px;
    text-align: center;
    line-height: 30px;
    z-index: 9999;
    right: 0;
    padding: 8px 10px;
    border-top: 2px solid #d1e0e2;
    width: 130px;
    margin-top: 0px;
    display: none;
}

.icenter dl dt {
    border-bottom: 1px dotted #c7d4ce;
    font-weight: 400;
    position: relative;
}

.icenter:hover dl {
    display: block;
}

.icenter dl dt:last-child {
    border: 0
}
.nologin{font-size: 12px;line-height: 2.4}
.icenter i {
    width: 0;
    display: inline-block;
    height: 0;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 6px solid #e3e3e3;
}
.mediaLogin {
    display: none;
}



